<template>
  <section class="page registration" v-cloak>

    <form action="#" class="form" @submit.prevent="reg">
      <v-input v-for="el in arrInput" :key="el" v-model="formLog[ el.where ]" :settings="el.settings"></v-input>
      <button class="form__btn" type="submit" :disabled="!disabledBtn">Зарегистрироваться</button>
    </form>

  </section>
</template>

<script>
import vInput from '@/scripts/components/vInput.vue'
import { mapActions, mapMutations } from 'vuex'

export default {
  name: 'RegistrationView',

  data: () => ({
    arrInput: [
      { 
        where: 'name',
        settings: { 
          title: 'Введите имя', dbClass: null, placeholder: 'Asik', min: 3, max: 7, type: 'text', id: 'regName'
        }
      },

      { 
        where: 'email',
        settings: { 
          title: 'Введите почту', dbClass: null, placeholder: 'asik@gmail.com', min: 0, max: 64, type: 'email', id: 'regEmail'
        }
      },

      { 
        where: 'password',
        settings: { 
          title: 'Введите пароль', dbClass: null, placeholder: 'C8YF9W', min: 6, max: 12, type: 'password', id: 'regPass'
        }
      },

      { 
        where: 'dbPassword',
        settings: { 
          title: 'Повторите пароль', dbClass: null, placeholder: 'C8YF9W', min: 6, max: 12, type: 'password', id: 'regDbPass'
        }
      },
    ],

    formLog: {
      dbPassword: '',
      password: '',
      email: '',
      name: '',
    },
  }),

  watch: {

  },
  
  methods: {
    ...mapActions([
      'registration'
    ]),

    ...mapMutations([
      'setError'
    ]),

    reg() {
      this.formLog.password === this.formLog.dbPassword 
        ? this.registration( this.formLog )
        : this.setError({ 
            message: { success: [ 'Пароли не совпадают. Пожалуйста введите верные данные в поля!' ] },
            code: 401 
          })
    }
  },

  computed: {
    disabledBtn() {
      return this.formLog.email && this.formLog.password && this.formLog.dbPassword && this.formLog.name
    }
  },

  components: {
    'v-input': vInput
  },

  created() {
  
  }
}
</script>